import React, { Component } from 'react';
import Logo from './Logo/Logo';
import Phone from './Phone/Phone';
import Nav from './Nav/Nav';
import header from './header.png';
import './Header.css'

const menu = [
  {
    link: '/pizza',
    label: ' ',
    className: 'pizza'
  },
  {
    link: '/salad',
    label: ' ',
    className: 'salad'
  },
  {
    link: '/drink',
    label: ' ',
    className: 'drink'
  },
  {
    link: '/order',
    label: ' ',
    className: 'order'
  }
];

class Header extends Component {
  render() {
    return (
      <header>
        <div className="container">
          <div className="row">
            <Logo />
            <Phone />
            <Nav items={menu}/>
          </div>
        </div>
      </header>
    );
  }
}

export default Header;



