import React, { Component } from 'react';
import './Order.css';



class Order extends Component {
  render() {
    // let d = new Date();
    // let hours = d.getHours();
    // let minutes = d.getMinutes();
    // let time = hours+":"+minutes
    return (
			<div className="container order">
				<div className="row">
					<div className="col-sm-8 col-sm-offset-2 top_title" id="top_title">
						<span>  </span>
						<p>  ,   ,        -                ..</p>
					</div>
				</div>
				<div className="row">
					<div className="col-sm-7">
						<form className="user" action="">
							<span>   </span>
							<p>,      ,      ,      .     :</p>
							<div className="col-xs-6"><label> :<input type="date" min="2016-12-01" max="2017-12-31" /></label></div>
							<div className="col-xs-6"><label> :<input type="time"  min="09:00" max="22:00" step="1800" /></label></div>
							<div className="col-xs-6"><label> :<input type="text" /></label></div>
							<div className="col-xs-6"><label> :<input type="tel" /></label></div>
							<div className="col-xs-12"><label> :<input type="text" /></label></div>
							<div className="col-xs-12"><label>  :<textarea name="text" /></label></div>
						</form>
					</div>
					<div className="col-sm-5">
						<div className="bill">
							<p> <span></span></p>
							<div className="scroll">
                <table className="order">
                  <tbody>
                    {this.props.order.map((item,index) =>
            					<tr className="item" key={index}>
                        <td>{item.id}</td>
                        <td>{item.price}</td>
                        <button onClick={()=>{this.props.onDelete(index)}}>Delete</button>
                      </tr>
                    )}
                </tbody>
                </table>
              </div>
							<div id="price-sum">:</div>
							<button></button>
							<button></button>
						</div>

					</div>
				</div>
			</div>
    );
  }
}

export default Order;



